"use server";

import {
  generateDocumentFromPrompt,
  type GenerateDocumentFromPromptInput,
  type GenerateDocumentFromPromptOutput,
} from "@/ai/flows/generate-document-from-prompt";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
import { initializeFirebase } from "@/firebase/index.server";

type HandleGenerationInput = {
  values: GenerateDocumentFromPromptInput;
  userId: string;
}

export async function handleGeneration(
  input: HandleGenerationInput
): Promise<{ success: boolean; data?: GenerateDocumentFromPromptOutput, error?: string }> {
  try {
    const { values, userId } = input;
    // Add a slight delay to allow progress indicators to show
    await new Promise(resolve => setTimeout(resolve, 1000));
    const result = await generateDocumentFromPrompt(values);

    // Save prompt to Firestore after successful generation
    if (result) {
      const { firestore } = initializeFirebase();
      const promptsRef = collection(firestore, 'users', userId, 'prompts');
      await addDoc(promptsRef, {
        ...values,
        createdAt: serverTimestamp(),
        userId: userId,
      });
    }

    return { success: true, data: result };
  } catch (error) {
    console.error(error);
    const errorMessage = error instanceof Error ? error.message : "An unknown error occurred.";
    return { success: false, error: `Failed to generate document: ${errorMessage}` };
  }
}
